.glowing-container {
    padding: 0.6em 0.5em;
    position: relative;
    display: inline-block;
    width: 750px; /* Same width as your images */
    height: 400px; /* Same height as your images */
    /* Make sure it wraps around the image */
    z-index: 0;
    border-radius: 10px;
    /* Match the image or adjust as needed */
}

.glowing-container:before {
    content: "";
    background: linear-gradient(45deg,
            #ff0000,
            #ff7300,
            #fffb00,
            #48ff00,
            #00ffd5,
            #002bff,
            #7a00ff,
            #ff00c8,
            #ff0000);
    position: absolute;
    top: -2px;
    left: -2px;
    background-size: 400%;
    z-index: -1;
    filter: blur(5px);
    -webkit-filter: blur(5px);
    width: calc(100% + 4px);
    height: calc(100% + 4px);
    animation: glowing-button-85 20s linear infinite;
    transition: opacity 0.3s ease-in-out;
    border-radius: 10px;
}

@keyframes glowing-button-85 {
    0% {
        background-position: 0 0;
    }

    50% {
        background-position: 400% 0;
    }

    100% {
        background-position: 0 0;
    }
}

.glowing-container:after {
    z-index: -1;
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background: #222;
    left: 0;
    top: 0;
    border-radius: 10px;
}


@keyframes gradientAnimation {
    0% {
      background-position: 100% 0%;
    }
  
    100% {
      background-position: -100% 0%;
    }
  }
  
  #gradient-Home
  {
      border:1px solid black;
      background: linear-gradient(130deg, #713ABE, #29e2ff, #d225ff);
      background-size: 200% 200%;
      border-radius:15px; 
      -webkit-animation: Animation 5s ease infinite;
      -moz-animation: Animation 5s ease infinite;
      animation: Animation 5s ease infinite;
      text-align: center;
  }